<template>
  <section class="faqTitle">
    <div class="container">
      <div class="row">
        <div class="col-xl-8 col-lg-7 col-md-6 col-12 text-md-left text-left">
          <h3 class="text-blue">Frequently Asked Questions</h3>
        </div>

        <div class="col-xl-4 col-lg-5 col-md-6 col-12">
          <label class="page-search">
            <input
              type="text"
              v-model="searchKeyword"
              placeholder="Quick Search"
            >
            <button @click="searchKeyword = ''"></button>
          </label>
        </div>

        <div
          class="col-12 text-center mt-30"
          id="tabs-01"
        >
          <ul class="tabs d-md-flex d-none">
            <li><a href="#faq_00"><span>Tümü</span></a></li>
            <li><a href="#faq_01"><span>Saç Ekimi</span></a></li>
            <li><a href="#faq_02"><span>Medical Estetik</span></a></li>
            <li><a href="#faq_03"><span>Estetik Cerrahi</span></a></li>
            <li><a href="#faq_04"><span>Dermatoloji</span></a></li>
          </ul>
        </div>
      </div>
    </div>

  </section>

</template>

<script>
export default {
  data() {
    return {
      searchKeyword: ""
    };
  },
  methods: {
    search(key) {
      let P = document.querySelector(".accordion");
      let Q = document.querySelectorAll(".question");
      let A = document.querySelectorAll(".accordion-detail p");
      let N = document.getElementById("noresults");
      let counter = 0;
      // Q.forEach(e=> e.innerText.includes(key)? e.parentElement.parentElement.style.display = "block" : e.parentElement.parentElement.style.display = "none");
      // A.forEach(e=> e.innerText.includes(key));
      let qNa = [];
      Q.forEach((e, i) => {
        qNa.push(e.innerText + " " + A[i].innerText);
      });
      qNa.forEach((e, i) => {
        e.includes(key)
          ? (Q[i].parentElement.parentElement.style.display = "block")
          : (Q[i].parentElement.parentElement.style.display = "none");
        Q[i].parentElement.parentElement.style.display == "none"
          ? counter++
          : counter--;
      });
      counter == Q.length
        ? (N.style.display = "block")
        : (N.style.display = "none");
    }
  },
  watch: {
    searchKeyword: function(p, n) {
      this.search(p);
    }
  }
};
</script>

